import React, { useState, useEffect } from 'react';
import { styled, alpha } from '@mui/material/styles';
import Box from '@mui/material/Box';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import Divider from '@mui/material/Divider';
import MenuItem from '@mui/material/MenuItem';
import Drawer from '@mui/material/Drawer';
import MenuIcon from '@mui/icons-material/Menu';
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import Sitemark from './SitemarkIcon';
import { Link as RouterLink } from 'react-router-dom';

const StyledToolbar = styled(Toolbar)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  flexShrink: 0,
  backdropFilter: 'blur(24px)',
  border: '1px solid',
  borderColor: theme.palette.divider,
  backgroundColor: alpha(theme.palette.background.default, 0.4),
  boxShadow: theme.shadows[1],
  padding: '8px 12px',
}));

export default function AppAppBar() {
  const [open, setOpen] = React.useState(false);
  const [currentPath, setCurrentPath] = React.useState(window.location.pathname);
  const { useLocation } = require('react-router-dom');
  const location = useLocation();


  const toggleDrawer = (newOpen) => () => {
    setOpen(newOpen);
  };

  useEffect(() => {
      const newPath = window.location.pathname;
      if (newPath !== currentPath) {
        setCurrentPath(newPath);
      }
  }, [location]);

  const buttonStyle = (path) => ({
    borderBottom: currentPath === path ? '2px solid' : 'none',
    borderRadius: 0,
    '&:hover': {
      borderBottom: '2px solid',
    },
  });

  console.log(currentPath);

  return (
    <AppBar
      position="fixed"
      sx={{ boxShadow: 0, bgcolor: 'transparent', backgroundImage: 'none', mt: 0, p: 3 }}
    >
        <StyledToolbar variant="dense" disableGutters>
          <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center', px: 0, gap: 3 }}>
            <Sitemark />
            <Box sx={{ display: { xs: 'none', md: 'flex' }, gap: 2 }}>
              <Button
                sx={buttonStyle('/tests')}
                component={RouterLink}
                to="/tests"
                variant="text"
                color="info"
                size="small"
              >
                Tests
              </Button>
              <Button
                sx={buttonStyle('/default-mock-data')}
                component={RouterLink}
                to="/default-mock-data"
                variant="text"
                color="info"
                size="small"
              >
                Default mock data
              </Button>
              <Button
                sx={buttonStyle('/mock-server')}
                component={RouterLink}
                to="/mock-server"
                variant="text"
                color="info"
                size="small"
              >
                 Mock server
              </Button>
              {/* <Button
                sx={buttonStyle('/coverage-report')}
                component={RouterLink}
                to="/coverage-report"
                variant="text"
                color="info"
                size="small"
              >
                Coverage report
              </Button>
              <Button
                sx={buttonStyle('/render-map')}
                component={RouterLink}
                to="/render-map"
                variant="text"
                color="info"
                size="small"
              >
                Render map
              </Button> */}
            </Box>
          </Box>
          <Box
            sx={{
              display: { xs: 'none', md: 'flex' },
              gap: 1,
              alignItems: 'center',
            }}
          >
            {/* <Button color="primary" variant="text" size="small">
              Sign in
            </Button>
            <Button color="primary" variant="contained" size="small">
              Sign up
            </Button> */}
          </Box>
          <Box sx={{ display: { sm: 'flex', md: 'none' } }}>
            <IconButton aria-label="Menu button" onClick={toggleDrawer(true)}>
              <MenuIcon />
            </IconButton>
            <Drawer anchor="top" open={open} onClose={toggleDrawer(false)}>
              <Box sx={{ p: 2, backgroundColor: 'background.default' }}>
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                  }}
                >
                  <IconButton onClick={toggleDrawer(false)}>
                    <CloseRoundedIcon />
                  </IconButton>
                </Box>
                <Divider sx={{ my: 3 }} />
                <MenuItem component={RouterLink} to="/tests">Tests</MenuItem>
                <MenuItem component={RouterLink} to="/default-mock-data">Default mock data</MenuItem>
                <MenuItem component={RouterLink} to="/mock-server">Mock server</MenuItem>
                <MenuItem component={RouterLink} to="/coverage-report">Coverage report</MenuItem>
                <MenuItem component={RouterLink} to="/render-map">Render map</MenuItem>
                <MenuItem>
                  <Button color="primary" variant="contained" fullWidth>
                    Sign up
                  </Button>
                </MenuItem>
                <MenuItem>
                  <Button color="primary" variant="outlined" fullWidth>
                    Sign in
                  </Button>
                </MenuItem>
              </Box>
            </Drawer>
          </Box>
        </StyledToolbar>
    </AppBar>
  );
}
